
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.header {
  width: 100%;
  height: 200px;
  background-color: #636d83;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  color: white;
}
.header .title {
  margin-left: 135px;
  margin-top: 35px;
}
.header .title .github {
  text-decoration: none;
  color: #fff;
}
.header .img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.header .h3 {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 0 10px;
}
.main {
  width: 80%;
  margin: 20px auto;
}
.main .title {
  -webkit-text-decoration: #22aeb4;
          text-decoration: #22aeb4;
}
.main .title::before {
  content: '';
  width: 20px;
  height: 100%;
  background-color: #22aeb4;
}
.main .title .updateTime {
  font-size: 14px;
  font-weight: 500;
  float: right;
}
.main .statistics {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  background-color: #f5f6f7;
  padding: 10px 40px;
  border-radius: 10px;
}
.main .statistics .number {
  color: #22aeb4;
  font-size: 20px;
  font-weight: 900;
}

.is-leaf .cell {
  color: #22aeb4;
  font-size: 16px;
}
.el-table_1_column_1,
.el-table_1_column_2,
.el-table_2_column_6 {
  font-weight: 900;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 20%;
}
.demo-table-expand .el-form-item.name {
  padding-left: 8px;
}
.demo-table-expand .el-form-item.confirm {
  padding-left: 30px;
}
.demo-table-expand .el-form-item.heal {
  padding-left: 40px;
}
.demo-table-expand .el-form-item.dead {
  padding-left: 50px;
}
